@use 'base/variables' as v;
@use 'base/mixins' as m;

.bienvenida {
    background-color: transparent;

    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;
    width: 100%;
    z-index: -1;
    display: flex;
    align-items: center;
    justify-content: center;

    @include m.tablet {
        align-items: center;
    }

    &__grid {
        display: flex;
        flex-direction: column;
        align-items: center;
        // margin-bottom: 4rem;
        // margin-top: 4rem;
        width: 100%;
        gap: 2rem;

        @include m.tablet {
            width: 70%;
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            margin: 0 auto;
        }
    }

    &__boxImagen {
        display: flex;
        flex-direction: column;
        align-items: center;

        @include m.tablet {
            display: block;
        }
    }

    &__imagen {
        background-color: transparent;
        overflow: hidden;
        width: 50%;
        animation: slideinUno 3s infinite alternate;
        animation: slideinDos 3s infinite alternate;
        transform: scale(.4);
        opacity: 0;
        transition-property: transform opacity;
        transition-duration: .3s;
        transition-timing-function: ease-in-out;

        @include m.telefonoXXL {
            width: 25rem;
        }

        &.activo {
            transform: scale(1);
            opacity: 1;
        }

        @keyframes slideinUno {
            from {
                border-radius: 20rem 23rem 20rem 40rem;
            }

            to {
                border-radius: 45rem 26rem 53rem 33rem;
            }
        }

        @keyframes slideinDos {
            from {
                border-radius: 45rem 26rem 53rem 33rem;
            }

            to {
                border-radius: 20rem 23rem 20rem 40rem;
            }
        }

        &:hover {
            cursor: pointer;
        }
    }

    &__titulo {
        width: 90%;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 2rem;
        position: relative;
        margin: 0 5%;
        top: 10rem;
        transform: scale(.8);
        opacity: 0;
        transition-property: top transform opacity;
        transition-duration: .7s;
        transition-timing-function: ease-in-out;

        &.activo {
            top: 0rem;
            opacity: 1;
            transform: scale(1);
        }

        @include m.telefonoXXL {
            align-items: flex-end;
            width: unset;
            margin-top: auto;
            margin-bottom: auto;
            margin-right: 6rem;
        }
    }

    &__ocupacionBox {
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: .4rem;

        padding-right: 4px;
        border-right: 4px solid v.$colorParrafo;
        overflow-x: hidden;
        white-space: nowrap;

        &.part1 {
            animation: borderRight .5s infinite forwards;
        }

        &.part2 {
            animation: parrafoWidthUno 2s steps(14) infinite alternate;
        }
    }


    &__parrafoCapitalize {
        text-transform: capitalize;
        font-size: 1.8rem;
    }

    &__parrafoOcupacion {
        font-size: 1.8rem;
        color: v.$colorParrafo;
    }

    &__H2 {
        width: 100%;
        text-align: center;

        @include m.telefonoXXL {
            text-align: end;
        }
    }
}



@keyframes imagenZoom {
    to {
        background-size: 100% 100%;
    }

    from {
        background-size: 200% 200%;
    }
}

@keyframes borderRight {
    to {
        border-color: v.$colorParrafo;
    }

    from {
        border-color: transparent;
    }
}

@keyframes parrafoWidthUno {
    0% {
        width: 15rem;
    }

    50% {
        width: 7rem;
    }

    100% {
        width: 12.2rem;
    }
}