@use 'base/variables' as v;
@use 'base/mixins' as m;

.testimonials {
    background-color: v.$colorFondoDiv;
    padding: v.$separacion 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: v.$separacion;

    &__boxGeneral {
        display: flex;
        flex-direction: row;
        position: relative;
        width: 100%;
        right: 0%;

        transition: right .4s ease-in-out;

        &.right0 {
            right: 0%;
        }

        &.right100 {
            right: 100%;
        }

        &.right200 {
            right: 200%;
        }

    }

    &__article {
        height: 26rem;
        min-width: 90%;
        padding: 0 5%;
        display: flex;
        flex-direction: column-reverse;
        align-items: center;
        gap: 1rem;

        @include m.tablet {
            display: grid;
            grid-template-columns: 2fr 1fr;
        }
    }

    &__infoText {
        width: 66%;
        display: flex;
        flex-direction: column-reverse;
        gap: .8rem;
        margin: 0 auto;
        font-family: v.$fuentePrincipal;
        color: v.$colorParrafo;
        text-align: justify;

        @include m.tablet {
            display: block;
            flex-direction: column;
            text-align: start;
        }
    }

    &__parrafoNombre {
        color: v.$colorParrafoTres;
        font-weight: 900;
        margin-left: .4rem;
        text-align: center;

        @include m.tablet {
            text-align: start;
        }
    }

    &__boxCuadroImagen {
        position: relative;
        width: 7rem;
        height: 7rem;
        border-radius: 0.8rem;

        @include m.tablet {
            width: 13rem;
            height: 13rem;
        }

        &::after {
            position: absolute;
            content: "";
            border-radius: 8px;
            top: 2rem;
            left: 2rem;
            width: 7rem;
            height: 7rem;

            @include m.tablet {
                width: 13rem;
                height: 13rem;
            }

            opacity: .5;
            background-color: rgba(13, 201, 145, .3);
            box-shadow: 0 0 4.2rem -0.4rem v.$colorBtn;
            z-index: 0;
        }
    }

    &__imagen {
        position: relative;
        border-radius: 0.8rem;
        width: 7rem;
        height: 7rem;
        z-index: 1;

        @include m.tablet {
            width: 13rem;
            height: 13rem;
        }
    }

    &__navSelectorArticle {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        gap: .4rem;
    }

    &__navSelectorArticleBtn {
        background-color: darken(v.$colorBtnDos, 6);
        width: 2rem;
        height: .4rem;
        border: none;
        transition: width .3s ease-in-out;

        &.activo {
            background-color: v.$colorBtn;
            width: 3.5rem;
        }

        &:hover {
            cursor: pointer;
        }
    }
}